$desktop: 'screen and (max-width: 1240px)';
$onlysmalldesktop: 'screen and (min-width: 1025px) and (max-width: 1149px)';
$tablet: 'screen and (max-width: 1024px)';
$phone: 'screen and (max-width: 767px)';
$smallphone: 'screen and (max-width: 340px)';

@mixin tablet {
    @media (max-width: 1199px) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: 767px) {
        @content;
    }
}

$main-font: 'Inter', 'Helvetica', sans-serif;

$background-primary: rgba(255, 255, 255, 1);
$darkner-4: rgba(0, 0, 0, 0.04);
$darkner-8: rgba(0, 0, 0, 0.08);
$darkner-16: rgba(0, 0, 0, 0.16);
$darkner-24: rgba(0, 0, 0, 0.24);
$accent-500: #d8598c;
$text-primary: #2f2227;
$text-primary-invert: #fff;
$text-secondary: rgba(47, 34, 39, 0.8);
$text-secondary-invert: rgba(255, 255, 255, 0.72);
$text-third: rgba(47, 34, 39, 0.56);

$background-secondary: rgba(245, 245, 245, 1);

$red-100: rgba(252, 221, 220, 1);
$red-200: rgba(249, 187, 185, 1);
$red-500: rgba(240, 84, 79, 1);
$red-600: rgba(192, 67, 63, 1);
$red-700: rgba(144, 50, 47, 1);

$link-100: rgba(208, 231, 249, 1);
$link-300: rgba(115, 184, 236, 1);
$link-500: rgba(21, 136, 224, 1);
$link-600: rgba(17, 109, 179, 1);

$green-100: rgba(214, 236, 222, 1);
$green-500: rgba(50, 159, 91, 1);
$green-600: rgba(40, 127, 73, 1);
